.Comment-container {
  display: flex;
  flex-flow: row nowrap;
  border-radius: 1rem;
  background-color: #aaccff;
  padding: 2rem 1rem;
  margin: 1rem;
}

.Comment-name {
  font-size: 1.5rem;
  font-weight: bold;
  margin-right: 1rem;
}


.Comment-userName {
  font-size: 0.5rem;
  font-weight: bold;
  margin-right: 1rem;
  border-radius: 0.3rem;
  cursor: pointer;
  background-color: rgb(168, 154, 101);
}